<template lang="pug">
.row
  .col-12
    h5 
    p              
    h5    
    p     
      ul
        li 24 LED 
        li  , 
        li USB       
        li Thor
        li 
        li 
    h5 
    p      
    h5   
    p       <b>u</b>     <b>utest</b>     Enter
    p       <b>p</b>   /   <b>p505000</b>  <b>p#1</b>     Enter
    p     13   <b>KZ123456789EZ</b>

    h3   
    h5    
    p   ,   ,  , 
    h5  
    p       ,     ,
    h5 Edge-case
    p   ,     ,   ,     

    h3  
    h5  
    p          ( Enter )   
    h5 
    p      
    h5 
    p     EPL  
    h5 WiFI 
    p     sortrack     Thor
    h5    
    p     sortrack     Thor

</template>

<script>

import Vue from 'vue'
import { mapGetters, mapActions } from 'vuex'
import Encoder from 'code-128-encoder'
var code128= new Encoder()

export default {
  name: 'Users',
  data () {
    return {
      // maxLeds: 24,
      deviceip:null,
      apiUrl:null
    }
  },
  computed:{
    ...mapGetters({
        settings: 'getSettingsSelected',
    })
  },
  methods:{
    ...mapActions([
      'settingsUpdate',
    ]),
    saveSettings(){    
      console.log('settgins updated');
      this.settingsUpdate(this.settings)
    },
    encode(val){
      return code128.encode(val)
    },
  }
}
</script>

<style scoped lang="stylus">

 .barcode
    font-family 'code128' !important
    font-size 22px  !important

</style>



